.contentNav
{
  margin: 0 auto;
  display: inline-block;
}
.contentNavLeft
{
  float: left;
  clear: left;
  padding-bottom: 5px;
}
.contentNavTitle
{
  text-align: center;
  padding-bottom: 5px;
  font-weight: 700;
}
.contentMainTitle
{
  text-align: center;
  padding-bottom: 5px;
  margin-top: 15px;
  font-weight: 700;
  //color: #2C3E50;
}
.contentNavTitleLeft
{
  text-align: center;
  padding-bottom: 5px;
  padding-left: 5px;
  font-weight: 700;
}
.contentNavRight
{
  float: left;
  clear: left;
  padding-bottom: 5px;
  padding-left: 20px;
}

.contentHeaderSeparator
{
  width: 99.7%;
  background-color: #27AE60;
  border-radius: 7px;
  border: 1px solid #27AE60;
  margin: 25px auto auto auto;

}
.contentHeaderSeparator > h3
{
  color: #ECF0F1;
}

.sponsorContentLogo
{

  height: 40px;
  margin: 0 auto;
  width: 200px;
}
.sponsorContentLogo img
{
  max-width:100%;
  max-height:100%;
}

p.test
{
  font-family: medium-content-serif-font,Georgia,Cambria,"Times New Roman",Times,serif;
  letter-spacing: .01rem;
  font-weight: 400;
  font-style: normal;
  font-size: 21px;
  line-height: 1.58;
  letter-spacing: -.003em;
}




